      url('../assets/fonts/icomoon.ttf?1ww89z') format('truetype'),
      url('../assets/fonts/icomoon.woff?1ww89z') format('woff'),
      url('../assets/fonts/icomoon.svg?1ww89z#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
